import React from 'react';
import { Col, Row, Spinner, Form } from 'reactstrap';
import { Button, TextField } from '@material-ui/core';
import CheckIcon from '@material-ui/icons/Check';
import ChevronLeftIcon from '@material-ui/icons/ChevronLeft';

const EmailCodeValidation = (props) => {
    return (
        <div>
            {props.visibility &&
                <Row className="pl-4 pr-4">
                    <Col xs={12} className="mt-2 ml-2 text-left">
                        <h4 className="mt-2 ml-4 mr-4 text-primary" style={{ fontWeight: "bold" }}>Step 2: Use the code sent to your email</h4>
                    </Col>
                    <Col xs={12} className="pl-5 pr-5">
                        <Form onSubmit={(e) => props.validate(e) } autoComplete="off">
                            <TextField
                                required
                                label="Validation Code"
                                placeholder="Enter the validation code"
                                name="vcode"
                                className="mb-2"
                                fullWidth
                            />
                            <Button type="submit" variant="contained" color="primary" className="pt-1" disabled={props.isOnValidate} fullWidth>
                                {props.isOnValidate ? <div className="text-center mt-1"><Spinner style={{ width: '1.5rem', height: '1.5rem' }} color="primary" /></div> : <div className="pt-1"><span>Validate Code <CheckIcon /></span></div>}
                            </Button>
                            <br />
                            <br />
                            { !props.isOnValidate && 
                                <div className="text-left">
                                    <Button color="primary" variant="contained" className="ml-1" onClick={() => props.goto("step1")}>
                                        <ChevronLeftIcon />Back
                                    </Button>
                                </div>
                            }
                        </Form>
                    </Col>
                </Row>
            }
        </div>
    );
}

export default EmailCodeValidation;