import React from 'react';
import Introduction from './TermsComponents/Introduction';
import Acceptance from './TermsComponents/Acceptance';
import Copyright from './TermsComponents/Copyright';
import ServiceMarks from './TermsComponents/ServiceMarks';
import License from './TermsComponents/License';
import Restrictions from './TermsComponents/Restrictions';
import Forms from './TermsComponents/Forms';
import Registration from './TermsComponents/Registration';
import ErrorsCorrectionsChanges from './TermsComponents/ErrorsCorrectionsChanges';
import ThirdParty from './TermsComponents/ThirdParty';
import UnlawfulActivity from './TermsComponents/UnlawfulActivity';
import NonTransferable from './TermsComponents/NonTransferable';
import Disclaimer from './TermsComponents/Disclaimer';
import UseOfInformation from './TermsComponents/UseOfInformation';
import PrivacyPolicy from './TermsComponents/PrivacyPolicy';
import ContactUs from './TermsComponents/ContactUs';

const TermContents = (props) => {
    return (
        <div className="text-justify" style={{ overflowX: "hidden" }}>
            <Introduction />
            <Acceptance />
            <Copyright />
            <ServiceMarks />
            <License />
            <Restrictions />
            <Forms />
            <Registration />
            <ErrorsCorrectionsChanges />
            <ThirdParty />
            <UnlawfulActivity />
            <NonTransferable />
            <Disclaimer />
            <UseOfInformation />
            <PrivacyPolicy />
            <ContactUs />
        </div>
    );
}

export default TermContents;