import React, { useEffect } from 'react';
import { Card, CardBody, Row, Col } from 'reactstrap';
import MUIDataTable from "mui-datatables";
import CircularProgress from '@material-ui/core/CircularProgress';
import Button from '@material-ui/core/Button';
import AndroidIcon from '@material-ui/icons/Android';
import DescriptionIcon from '@material-ui/icons/Description';
import APK from '../../../../assets/file/app.apk';
import USERMANUAL from '../../../../assets/file/user-manual.pdf';
const DataTable = (props) => {

    useEffect(() => {
        props.loadProfiles();
    }, [])

    return (
        <div className="col-md-12 p-0 m-0">
            <div className="mr-3 ml-3 mb-2 col-md-12">
                <Row>
                    <Col xs={2} className="p-0 pl-1 pr-1 m-0 mr-1 ">
                        <a href={APK} download="app.apk" style={{ textDecoration: "none" }}>
                            <Button variant="contained" className="bg-success text-white" fullWidth>
                                <AndroidIcon />
                                <span style={{ fontSize: "1.1em", marginLeft: "5px" }}> Download App</span>
                            </Button>
                        </a>
                    </Col>
                    <Col xs={2} className="p-0 m-0 mr-1 ml-1">
                        <a href={USERMANUAL} download="lgu-user-manual.pdf" style={{ textDecoration: "none" }}>
                            <Button variant="contained" className="bg-warning text-white" fullWidth>
                                <DescriptionIcon />
                                <span style={{ fontSize: "1.1em", marginLeft: "5px" }}> User Guide</span>
                            </Button>
                        </a>
                    </Col>
                    <Col xs={4}>
                    </Col>
                </Row>
            </div>
            {props.isLoading && <CircularProgress style={{ marginLeft: '48%', marginTop: '18%', position: "absolute", zIndex: '1000', }} />}
            <Row>
                <div className="col-md-12 ml-3 mr-3">
                    <Card className="p-0 m-0" style={{ borderRadius: '0px' }}>
                        <CardBody>
                            <MUIDataTable
                                title={"Profile List"}
                                data={props.data}
                                columns={props.columns}
                                options={props.options}
                            />
                        </CardBody>
                    </Card>
                </div>
            </Row>
        </div>
    )

}

export default DataTable
