import React, { useState } from 'react';

import {
    Navbar,
    NavbarBrand,
    Nav,
    UncontrolledDropdown,
    DropdownToggle,
    DropdownMenu,
    DropdownItem
} from 'reactstrap';
import Swal from 'sweetalert2';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faSignOutAlt, faTh, faUserLock } from '@fortawesome/free-solid-svg-icons';
import Icon from '../../../assets/icon.png';
import '../dashboard.css';
import AccountModal from '../Content/Account/AccountModal';

const Header = (props) => {

    const [changePasswordModal, setVisiblechangePasswordModal] = useState(false);
    const toggleChangePasswordModal = () => setVisiblechangePasswordModal(!changePasswordModal);

    const changePasswordHandler = (e) => {
        e.preventDefault();
        toggleChangePasswordModal();
    }

    const logoutHandler = (e) => {
        e.preventDefault();
        Swal.fire({
            title: 'Are you sure?',
            text: "You want to logout?",
            icon: 'warning',
            showCancelButton: true,
            confirmButtonColor: '#3085d6',
            cancelButtonColor: '#d33',
            confirmButtonText: 'Yes'
        }).then((result) => {
            if (result.value) {
                localStorage.removeItem('lgutokens');
                window.location.reload();
            }
        })
    }

    return (
        <div>
            <Navbar className="p-1 m-0 bg-primary text-white" dark expand="md">
                <div className="container-fluid">
                    <NavbarBrand className="ml-5">
                        <div>
                            <img className="float-left mr-1 mt-1" src={Icon} width="22" height="21" alt="Logo" />
                            <div className="float-left mt-0">
                                <b className="mt-1">Passenger eTracer System<span style={{ fontWeight: "normal", marginLeft: "5px", fontSize: "18px" }}>|</span></b><span className="ml-1 mt-1" style={{ color: "#f9bd05", fontWeight: "bold", fontSize: "18px" }}>{props.pageTitle}</span>
                            </div>
                        </div>
                    </NavbarBrand>
                    <Nav navbar>
                        <UncontrolledDropdown nav inNavbar>
                            <DropdownToggle nav>
                                <span className="text-white mr-1">{props.user.carrier_name || props.user}</span>
                                <FontAwesomeIcon icon={faTh} />
                            </DropdownToggle>
                            <DropdownMenu right className="m-0 p-0 mt-1">
                                <DropdownItem className="p-0 m-0">
                                    <a href="/" onClick={(e) => changePasswordHandler(e)} className="text-primary nav-link">
                                        <FontAwesomeIcon className="mr-1" icon={faUserLock} />
                                        <span>Change</span>
                                    </a>
                                </DropdownItem>
                                <DropdownItem className="p-0 m-0" divider />
                                <DropdownItem className="p-0 m-0">
                                    <a href="/" onClick={(e) => logoutHandler(e)} className="text-primary nav-link">
                                        <FontAwesomeIcon className="mr-1" icon={faSignOutAlt} />
                                        <span>Logout</span>
                                    </a>
                                </DropdownItem>
                            </DropdownMenu>
                        </UncontrolledDropdown>
                    </Nav>
                </div>
            </Navbar>
            <div>
                <AccountModal isOpen={changePasswordModal} toggle={toggleChangePasswordModal} />
            </div>
        </div>
    )
}

export default Header

