import React from 'react';
import VerifiedUserIcon from '@material-ui/icons/VerifiedUser';
import { Paper } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import { Row, Col } from 'reactstrap';
import { Link } from 'react-router-dom';

const useStyles = makeStyles((theme) => ({
    root: {
        display: 'flex',
        '& > *': {
            margin: theme.spacing(1),
        },
    },
    small: {
        width: theme.spacing(2.3),
        height: theme.spacing(2.3),
    },
    medium: {
        width: theme.spacing(5),
        height: theme.spacing(5),
    },
    large: {
        width: theme.spacing(7),
        height: theme.spacing(7),
    },
}));

const SuccessPage = (props) => {
    const classes = useStyles();
    return (
        <div>
            {props.visibility &&
                <div>
                    <Paper className="p-4">
                        <Row>
                            <Col xs={1} className="pl-1 pr-1">
                                <span className="text-success"><VerifiedUserIcon className={classes.large} /></span>
                            </Col>
                            <Col xs={11}>
                                <h3>
                                    <span>
                                        Successfully Registered! <br />
                                        <small>Please wait for the admin approval to login to the system.</small> <br />
                                        <h5>
                                            <small>
                                                Click here to
                                                <Link to="/login" className="ml-1">
                                                    Login Page
                                                </Link>
                                            </small>
                                        </h5>
                                    </span>
                                </h3>
                            </Col>
                        </Row>
                    </Paper>
                </div>
            }
        </div>
    );
}

export default SuccessPage;