import React from 'react';
import TermHeaders from '../TermHeaders';
import TermTexts from '../TermTexts';

const PrivacyPolicy = (props) => {
    return (
        <div className="text-justify">
            <TermHeaders>15. Privacy Policy</TermHeaders>
            <TermTexts>
                Our Privacy Policy, as it may change from time to time, is a part of this Agreement.
                <br /><br />
                MARINA is committed to ensure that your privacy is protected. Should we ask you to provide certain information by which you can be
                identified when using this Site, you can be assured that it will only be used in accordance with this privacy statement.
                <br /><br />
                MARINA may change this policy from time to time by updating this page. You should check this page from time to time to ensure that
                you are agreeable with any changes.
            </TermTexts>
        </div>
    )
}

export default PrivacyPolicy;