import React from 'react';
import TermHeaders from '../TermHeaders';
import TermTexts from '../TermTexts';

const Restrictions = (props) => {
    return (
        <div className="text-justify">
            <TermHeaders>6. Restrictions and Prohibitions on Use</TermHeaders>
            <TermTexts>
                Your license for access and use of the Site and any information, materials or documents including the pictures therein
                are subject to the following restrictions and prohibitions on use: You may not
                    <div className="pl-3">
                    <ol type="a">
                        <li>
                            copy, print (except for the express limited purpose permitted by Section 4 above), republish, display, distribute, transmit, or otherwise make available in any form or by any means all or any portion of the Site;
                            </li>
                        <li>
                            use the Site or any materials obtained from the Site to develop, as a component of, any information, storage and retrieval system, database, information base, or similar resource (in any media now existing or hereafter developed), that is offered for commercial distribution of any kind, including through any other commercial distribution mechanism;
                        </li>
                        <li>
                            create compilations or derivative works of any Content and Materials from the Site;
                        </li>
                        <li>
                            use any Content and Materials from the Site in any manner that may infringe any copyright, intellectual property right, proprietary right, or property right of us or any third parties;
                        </li>
                        <li>
                            remove, change or obscure any copyright notice or other proprietary notice or terms of use contained in the Site;
                        </li>
                        <li>
                            make any portion of the Site available through any time sharing system, service bureau, the Internet or any other technology now existing or developed in the future;
                        </li>
                        <li>
                            remove, decompile, disassemble or reverse engineer any Site software or use any network monitoring or discovery software to determine the Site architecture;
                        </li>
                        <li>
                            use any automatic or manual process to harvest information from the Site;
                        </li>
                        <li>
                            use the Site for the purpose of gathering information for or transmitting
                            <ol>
                                <li> unsolicited commercial email;</li>
                                <li> email that makes use of headers, invalid or nonexistent domain names, or other means of deceptive addressing; and</li>
                                <li>unsolicited telephone calls or facsimile transmissions;</li>
                            </ol>
                        </li>
                        <li>
                            use the Site in a manner that violates any national law regulating email, facsimile transmissions or telephone solicitations; and
                        </li>
                        <li>
                            export or re-export the Site or any portion thereof, or any software available
                        </li>
                    </ol>
                </div>
            </TermTexts>
        </div>
    )
}

export default Restrictions;