import React from 'react';
import TermHeaders from '../TermHeaders';
import TermTexts from '../TermTexts';

const Registration = (props) => {
    return (
        <div className="text-justify">
            <TermHeaders>8. Registration</TermHeaders>
            <TermTexts>
                Certain sections of, or offerings from, the Site may require you to register. If registration is requested, you agree to provide us with accurate,
                complete registration information. Your registration must be done using accurate information. Each registration is for your personal use only.
                You are responsible to prevent unauthorized use of the Site using your registration information.
            </TermTexts>
        </div>
    )
}

export default Registration;