import React, { useState } from 'react';
import Axios from 'axios';
import API from '../../Api';

import { makeStyles } from '@material-ui/core/styles';
import FacebookIcon from '@material-ui/icons/Facebook';
import TwitterIcon from '@material-ui/icons/Twitter';
import InstagramIcon from '@material-ui/icons/Instagram';
import YouTubeIcon from '@material-ui/icons/YouTube';
import WarningRoundedIcon from '@material-ui/icons/WarningRounded';

import {
    Container,
    Row,
    Col,
    Card,
    Alert,
} from 'reactstrap';

import Logo from '../../assets/icon.png';
import EmailVerification from './pages/EmailVerification';
import EmailCodeValidation from './pages/EmailCodeValidation';
import RegistrationForm from './pages/RegistrationForm';
import SuccessPage from './pages/SuccessPage';
import { Paper } from '@material-ui/core';

const useStyles = makeStyles((theme) => ({
    root: {
        display: 'flex',
        '& > *': {
            margin: theme.spacing(1),
        },
    },
    small: {
        width: theme.spacing(2.3),
        height: theme.spacing(2.3),
    },
    medium: {
        width: theme.spacing(5),
        height: theme.spacing(5),
    },
    large: {
        width: theme.spacing(7),
        height: theme.spacing(7),
    },
}));

const Register2 = (props) => {

    const classes = useStyles();

    const [step, setStep] = useState("step1");
    const [emailInput, setEmailInput] = useState("");
    const [isError, setIsError] = useState(false);
    const [errorMessage, setErrorMessage] = useState("Oops! something went wrong :( ");
    const [isOnVerify, setIsOnVerify] = useState(false);
    const [isOnValidate, setIsOnValidate] = useState(false);
    const [isOnRegistering, setIsOnRegistering] = useState(false);

    const emailVerifcationHandler = (e) => {
        try {
            e.preventDefault();
            setIsOnVerify(true);
            const canceler = Axios.CancelToken;
            const source = canceler.source();

            let data = {
                '_email': document.querySelector("input[name=email]").value
            }

            if (data._email === emailInput) {
                setIsOnVerify(false);
                goto("step2");
                return null;
            }

            API.post("lgu/verify", data, {
                cancelToken: source.token
            }).then(result => {
                setIsOnVerify(false);

                // added to db but unconfirmed
                if (result.status === 255) {
                    goto("step2");
                    return null;
                }

                if (result.status === 254) {
                    setIsError(true);
                    setErrorMessage(result.data.message);
                    return null;
                }

                if (result.status === 200) {
                    setIsError(false);
                    goto("step2");
                    setEmailInput(data._email);
                } else {
                    setIsError(true);
                    setErrorMessage(result.data.message);
                }
            }).catch(e => {
                setIsOnVerify(false);
                setIsError(true);
                setErrorMessage(errorMessage);
            });
        } catch (error) {
            setIsError(true)
        }
    }

    const codeValidationHandler = (e) => {
        try {
            e.preventDefault();
            setIsOnValidate(true);
            const canceler = Axios.CancelToken;
            const source = canceler.source();

            let data = {
                "_code": document.querySelector("input[name=vcode]").value,
                "_email": emailInput
            }

            API.post("lgu/code", data, {
                cancelToken: source.token
            }).then(result => {
                setIsOnValidate(false);
                if (result.data.status !== false) {
                    setIsError(false);
                    goto("step3");
                } else {
                    setIsError(true);
                    setErrorMessage("Code is invalid, please check the code on your email");
                }
            }).catch(e => {
                setIsOnValidate(false);
                setIsError(true);
                setErrorMessage(errorMessage);
            });
        } catch (error) {
            setIsError(true);
        }
    }

    const registerHandler = (e) => {
        try {
            e.preventDefault();
            setIsError(false);

            let email = document.querySelector("input[name='email']").value;
            let name = document.querySelector("input[name='username']").value;
            let password = document.querySelector("input[name='password']").value;
            let mobile = document.querySelector("input[name='mobile']").value;
            let area = document.querySelector("input[name='area']").value;
            let areaType = document.querySelector("input[name='taskforce']").value;
            let agreements = document.querySelector("input[name='termsOfAgreement']").checked;

            let data = {
                "email": email,
                "name": name,
                "password": password,
                "contact": mobile,
                "area": area,
                "areaType": areaType === "province" ? 0 : 1
            }

            if (data.email !== "" && data.name !== "" && data.password !== "" && data.contact !== "" && data.province !== "") {
                if (!agreements) {
                    setErrorMessage("Please mark with check the checkbox if you agree");
                    setIsError(true);
                    return null;
                }
                setIsOnRegistering(true);
                API.post("lgu/register", data).then(result => {
                    setIsOnRegistering(false);
                    if (result.status === 200) {
                        if (result.data.status === false) {
                            setErrorMessage(result.data.error);
                            setIsError(true);
                        } else {
                            setEmailInput("");
                            goto("stepSuccess");
                        }
                    } else {
                        setIsError(true);
                    }
                }).catch(e => {
                    setErrorMessage(e.toString());
                    setIsError(true);
                    setIsOnRegistering(false);
                })
            } else {
                setErrorMessage("Please fill out the required fields");
                setIsError(true);
            }
        } catch (error) {
            setIsError(true);
        }
    }

    const goto = (step) => {
        try {
            setIsError(false);
            setStep(step);
        } catch (error) {
            setIsError(true);
        }
    }

    const handleEmailVerificationError = () => {
        setIsError(false);
    }


    return (
        <Container style={{ width: '100%', marginTop: '1%' }}>
            <Card className="bg-primary p-0 m-0" style={{ borderRadius: "0px", border: "none" }}>
                <Row className="p-0 m-0">
                    <Col xs={12} className="text-center text-white p-2">
                        <Row>
                            <Col xs={10}>
                                <img className="mt-2 ml-2 float-left" src={Logo} width="40" height="40" alt="System Logo"></img>
                                <a href="https://marina.gov.ph/" target="_blanks" className="text-white ml-1 mr-1 m-0 p-0 float-left" style={{ textDecoration: "none" }}><h3 className="mt-3">MARINA: Passenger e-Tracer System</h3></a>
                            </Col>
                            <Col xs={2}>
                                <div className="text-center mt-2 ml-4">
                                    Follow us on: <br />
                                    <a href="https://www.facebook.com/DOTrMARINAPH" rel="noopener noreferrer" target="_blanks" className="text-white mr-1" style={{ textDecoration: "none", }}><FacebookIcon className={classes.med} /></a>
                                    <a href="https://twitter.com/DOTrMARINAPH" rel="noopener noreferrer" target="_blanks" className="text-white ml-1 mr-1" style={{ textDecoration: "none", }}><TwitterIcon className={classes.small} /></a>
                                    <a href="https://www.instagram.com/dotrmarinaph/" rel="noopener noreferrer" target="_blanks" className="text-white ml-1 mr-1" style={{ textDecoration: "none", }}><InstagramIcon className={classes.small} /></a>
                                    <a href="https://www.youtube.com/channel/UCK9SPcPFhiWLBUXAzlQqsxg" rel="noopener noreferrer" target="_blanks" className="text-white ml-1 mr-1" style={{ textDecoration: "none", }}><YouTubeIcon className={classes.small} /></a>
                                </div>
                            </Col>
                        </Row>
                    </Col>
                    <Col xs={12} className="p-0 m-0">
                        <Card className="p-0 m-0" style={{ borderRadius: "0px" }}>
                            <Container style={{ width: "70%" }} className="p-4 pt-0">
                                {isError &&
                                    <Col xs={12} className="pl-5 pr-5 ml-1 mr-1 mb-1 text-center">

                                        <Paper color="secondary" className="text-white p-0 m-0">
                                            <Alert color="danger" className="p-0 pt-2 text-danger">
                                                <h5><WarningRoundedIcon className="p-0 m-0 mr-1" />{errorMessage}</h5>
                                            </Alert>
                                        </Paper>

                                    </Col>
                                }
                                <EmailVerification handleEmailVerificationError={handleEmailVerificationError} emailInput={emailInput} visibility={step === "step1"} verify={emailVerifcationHandler} isOnVerify={isOnVerify} />
                                <EmailCodeValidation visibility={step === "step2"} goto={goto} validate={codeValidationHandler} isOnValidate={isOnValidate} />
                                <RegistrationForm registerHandler={registerHandler} emailInput={emailInput} visibility={step === "step3"} isOnRegistering={isOnRegistering} />
                                <SuccessPage visibility={step === "stepSuccess"} />
                            </Container>
                        </Card>
                    </Col>
                </Row>
            </Card>

        </Container>
    );
}

export default Register2;