import React, { useState } from 'react'
import Axios from 'axios';
import { useAuth } from "../../../context/auth";
import Alert from '@material-ui/lab/Alert';
import AlertTitle from '@material-ui/lab/AlertTitle';
import { CircularProgress } from '@material-ui/core';
import Swal from 'sweetalert2';

const VerifyEmail = (props) => {
    const token = useAuth().authTokens;
    const resendPath = "https://passengertracer.net/apis/api/email/resend";
    const [isProcessing, setIsProcessing] = useState(false);
    const [haveResend, setHaveResend] = useState(false);

    const resendHandler = (e) => {

        e.preventDefault();
        if (!haveResend) {
            setIsProcessing(true);
            setHaveResend(true);
            Axios.get(resendPath, {
                headers: {
                    'Authorization': 'bearer ' + token.access_token
                }
            }).then(result => {
                setIsProcessing(false);
                Swal.fire(
                    'Email sent',
                     result.data.message,
                    'success'
                );
            }).catch(e => {
                setIsProcessing(false);
                Swal.fire(
                    'Failed request',
                     e.message,
                    'error'
                );
            });
        }else{
            Swal.fire(
                'Email Verification',
                'Email already sent, reload page',
                'warning'
            );
        }
    }

    return (
        <div className="container mt-4 p-0 m-0" style={{ minWidth: "1450px" }} >
            <div className="row p-0 m-0">
                {
                    isProcessing ?
                        <div className="text-center text-primary" style={{ width: "100%", height: "100%", zIndex: "1001", position: "fixed", top: "0", left: "0" }}>
                            <div style={{ backgroundColor: "#fff", opacity: "0.2", width: "100%", height: "100%", zIndex: "1000", position: "fixed", top: "0", left: "0" }}></div>
                            <CircularProgress style={{ marginTop: "20%", zIndex: "1500", }} />
                            <h4>Resending email verification...</h4>
                        </div>
                        :
                        null
                }
                <Alert severity="info" style={{ width: '100%' }} className="mt-3">
                    <AlertTitle>System Message</AlertTitle>
                    Please verify your email first. In case the verification email expires click <a href="#resend" onClick={resendHandler}>resend</a>.
                </Alert>
            </div>
        </div>
    )
}

export default VerifyEmail