import React, { useEffect, useState, useCallback } from 'react';
import Axios from 'axios';

import Autocomplete from '@material-ui/lab/Autocomplete';
import TextField from '@material-ui/core/TextField';


const City = (props) => {
    const url = "https://passengertracer.net/apis/api/municipal";
    const [cityList, setCityList] = useState([]);
    const [selectedCityMunicipalityZip, setSelectedCityMunicipalityZip] = useState('');
    const loadCities = useCallback(
        () => {
            Axios.get(url).then(result => {
                if (result.status === 200) {
                    if (result.data !== null) {
                        let cities = result.data.map(data => {
                            return {
                                value: data.id,
                                label: data.city
                            }
                        });
                        setCityList(cities);
                    }
                }
            }).catch(e => {
                return null;
            });

        },
        [],
    );

    useEffect(() => {
        loadCities();
    }, []);

    return (
        <Autocomplete
            options={cityList}
            autoHighlight
            getOptionLabel={(option) => option.label}
            renderOption={(option) => (
                <React.Fragment>
                    {option.label} ({option.value})
                </React.Fragment>
            )}
            onChange={(event,  newValue) => {
               setSelectedCityMunicipalityZip(newValue.value);
            }}
            renderInput={(params) => (
                <div>
                    <TextField
                        {...params}
                        label="Choose a city/municipality"
                        placeholder="City / Municipality"                        
                        required
                    />
                    <input type="hidden" name="area" value={selectedCityMunicipalityZip} />
                </div>
            )}
            fullWidth
        />
    );
}

export default City;