import React from 'react';
import {
    Modal,
    ModalHeader,
    ModalBody,
    ModalFooter,
} from 'reactstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faBook } from '@fortawesome/free-solid-svg-icons';
import TermsContent from './TermContents';
import Button from '@material-ui/core/Button';
const Terms = (props) => {

    return (
        <div>
            <Modal backdrop={"static"} isOpen={props.isOpen} toggle={props.toggle} className="modal-xl">
                <ModalHeader className="text-primary"><FontAwesomeIcon icon={faBook} /> TERMS OF USE AGREEMENT</ModalHeader>
                <ModalBody>
                    <div style={{ maxHeight: "500px", minHeight: "500px", overflowY: "scroll" }}>
                        <TermsContent />
                    </div>
                </ModalBody>
                <ModalFooter>
                    <Button color="secondary" variant="contained" onClick={props.toggle} style={{ minWidth: "120px" }}>
                        Close
                    </Button>
                </ModalFooter>
            </Modal>
        </div>
    )
}

export default Terms;