import React from 'react';
import TermHeaders from '../TermHeaders';
import TermTexts from '../TermTexts';

const ThirdParty = (props) => {
    return (
        <div className="text-justify">
            <TermHeaders>10. Third Party Content</TermHeaders>
            <TermTexts>
                Third party content from member agencies may appear on the Site or may be accessible via links from the Site.
                We are not responsible for and assume no liability for any mistakes, misstatements of law, defamation, omissions, falsehood in the statements,
                opinions, representations or any other form of content thereon. We are not liable for any damages or loss arising from access to those websites.
            </TermTexts>
        </div>
    )
}

export default ThirdParty;