import React, { useState } from 'react' 

import TextField from '@material-ui/core/TextField' 
import Checkbox from '@material-ui/core/Checkbox' 
import Button from '@material-ui/core/Button' 

import {
    Col,
    Form,
    Spinner
} from 'reactstrap' 
import HowToRegIcon from '@material-ui/icons/HowToReg'
import Provinces from '../Provinces' 
import Terms from '../../Dashboard/Content/Terms/Terms' 
// import DataSharing from '../../Dashboard/Content/DataSharing/DataSharing' 
import City from '../City' 
import TaskForceUnitType from '../TaskForceUnitType' 

const RegistrationForm = (props) => {
    
    const [agreements, setAgreements] = useState(false) 
    const [showTerms, setShowTerms] = useState(false)     
    const [unitType, setUnitType] = useState('') 

    // const [dataSharingTerms, setDataSharingTerms] = useState(false)     

    const agreementsHandler = () => {
        setAgreements(!agreements) 
    }

    const showTermsHandler = (e) => {
        e.preventDefault() 
        toggleShowTermsHandler() 
    }

    const toggleShowTermsHandler = () => {
        setShowTerms(!showTerms) 
    }

    // const dataSharingTermsHandler = (e) => {
    //     e.preventDefault() 
    //     toggleDataSharingHandler() 
    // }

    // const toggleDataSharingHandler = () => {
    //     setDataSharingTerms(!dataSharingTerms) 
    // }

    const unitTypeHandler = (type) => {
        setUnitType(type) 
    }

    return (
        <div>
            {props.visibility &&
                <div>
                    <Col xs={12} className="mt-2 ml-4 pl-4 text-left">
                        <h4 className="mt-3 ml-4 mr-4 text-primary" style={{ fontWeight: "bold" }}>Step 3: Fill out registration form</h4>
                    </Col>
                    <Col xs={12} className="pl-5 pr-5">
                        <Form onSubmit={(e) => props.registerHandler(e)} autoComplete="off">
                            <Col xs={12} className="pl-4 pr-4 mt-1">
                                <TextField
                                    required
                                    disabled
                                    type="email"
                                    label="Email"
                                    defaultValue={props.emailInput}
                                    placeholder="Enter your email"
                                    name="email"
                                    fullWidth

                                />
                            </Col>
                            <Col xs={12} className="pl-4 pr-4 mt-1">
                                <TextField
                                    required
                                    label="Username"
                                    placeholder="Enter your username"
                                    name="username"
                                    fullWidth
                                />
                            </Col>
                            <Col xs={12} className="pl-4 pr-4 mt-1">
                                <TextField
                                    required
                                    type="password"
                                    label="Password"
                                    placeholder="Enter your password"
                                    name="password"
                                    fullWidth
                                />
                            </Col>
                            <Col xs={12} className="pl-4 pr-4 mt-1">
                                <TextField
                                    required
                                    type="number"
                                    label="Mobile Number"
                                    placeholder="+639xxxxxxxxx"
                                    name="mobile"
                                    inputProps={{
                                        maxLength: 11,
                                    }}
                                    fullWidth
                                />
                            </Col>
                            <Col xs={12} className="p-0 m-0 pl-4 pr-4 mt-1">
                                <TaskForceUnitType unitTypeHandler={unitTypeHandler} />
                            </Col>
                            {unitType === "province" &&
                                <Col xs={12} className="p-0 m-0 pl-4 pr-4 mt-1">
                                    <Provinces />
                                </Col>
                            }
                            {unitType === "city" &&
                                <Col xs={12} className="p-0 m-0 pl-4 pr-4 mt-1">
                                    <City />
                                </Col>
                            }
                            <Col xs={12} className="pl-4 pr-4 mt-2 mb-4">
                                <div className="row">
                                    <div className="col-md-1">
                                        <Checkbox
                                            checked={agreements}
                                            onChange={agreementsHandler}
                                            name="termsOfAgreement"
                                            color="primary"
                                        />
                                    </div>
                                    <div className="col-md-11 pl-0 pt-2 pb-3" style={{ marginLeft: "-3px" }}>
                                        <span className="float-left">I have read and agreed to the MARINA <a href="#termsAgreement" onClick={showTermsHandler}>Terms of Use Agreement</a></span>
                                    </div>
                                </div>
                                <Button type="submit" variant="contained" color="primary" className="pt-1" disabled={props.isOnRegistering} fullWidth>
                                    {props.isOnRegistering ? <div className="text-center mt-1"><Spinner style={{ width: '1.5rem', height: '1.5rem' }} color="light" /></div> : <div className="pt-1"><span>REGISTER</span><HowToRegIcon className="ml-1" /></div>}
                                </Button>
                            </Col>
                        </Form>
                    </Col>

                    <Terms isOpen={showTerms} toggle={toggleShowTermsHandler} />
                    {/* <DataSharing isOpen={dataSharingTerms} toggle={toggleDataSharingHandler} /> */}
                </div>
            }
        </div>
    ) 
}

export default RegistrationForm 