import React, { useState, useEffect, useCallback } from 'react'
import DateFnsUtils from '@date-io/date-fns'
import { useAuth } from "../../../context/auth"
import Axios from 'axios';
import API from '../../../Api'

import DataTable from './DataTable/DataTable'
import Footer from '../Footer/Footer'
import VerifyEmail from './VerifyEmail'

const Content = (props) => {
    const token = useAuth().authTokens
    const dateFns = new DateFnsUtils()

    const [isEmailVerified, setIsEmailVerified] = useState(false)
    const [profileData, setProfileData] = useState()
    const [isLoading, setIsLoading] = useState(false) 

    const columns = [
        "Reference #",
        "Name",
        "Type",
        "Contact",
        "Origin",
        "Destination",
        "ETD",
        "ETA",
        "Transport #",
        "Seat #",
        "Ship"
    ] 

    const options = {
        fixHeader: true,
        filterType: 'dropdown',
        responsive: 'vertical',
        tableBodyHeight: '350px',
        selectableRows: 'none',
        print: false,
        
    } 

    const loadProfiles = useCallback(() => {
        setIsLoading(true) 
        const canceler = Axios.CancelToken 
        const source = canceler.source() 

        API.get("lgu/lsi", {
            headers: {
                'Authorization': 'bearer ' + token.access_token
            },
            cancelToken: source.token
        }).then(result => {

            let profiles = result.data 
            let profileArray = [] 
            if (result.data !== null) {
                Object.keys(profiles).forEach(key => {
                    let p = [
                        profiles[key].id,
                        `${profiles[key].first} ${profiles[key].last}`,
                        profiles[key].person_type || 'N/A',
                        profiles[key].contact || 'N/A',
                        profiles[key].origin.toUpperCase() || 'N/A',
                        profiles[key].destination.toUpperCase() || 'N/A',
                        dateFns.format(new Date(profiles[key].departure), "M/d/yyyy"),
                        dateFns.format(new Date(profiles[key].arrival), "M/d/yyyy"),
                        profiles[key].transpo_no.transport_no,
                        profiles[key].transpo_seat,
                        profiles[key].transpo_no.transport_vehicle
                    ] 

                    profileArray.push(p) 
                }) 
            }

            setIsLoading(false) 
            setProfileData(profileArray) 
        }).catch(e => {
            setIsLoading(false) 
        }) 

        return () => { source.canel("Loading profile aborted")  }
    }, [])

    const isVerified = useCallback(() => {

        API.get("auth/lgu/check-email", {
            headers: {
                'Authorization': 'bearer ' + token.access_token
            }
        }).then(result => {
            if (result.status !== 254) {
                setIsEmailVerified(false) 
            }else{
                setIsEmailVerified(true) 
            }
        }).catch(e => {
            setIsEmailVerified(false) 
        }) 
    }, [])

    useEffect(() => {
        isVerified() 
    }, [])

    return (
        <div className="container mt-4" style={{ minWidth: "1450px" }} >
            <div>
                {isEmailVerified ? <VerifyEmail /> : <div className="row"><DataTable loadProfiles={loadProfiles} data={profileData} isLoading={isLoading} columns={columns} options={options} />
                    <Footer /></div>}
            </div>
        </div>
    )
}

export default Content