import React, { useState } from 'react'
import API from '../../../../Api';
import Swal from 'sweetalert2';
import {
    Button,
    Modal,
    ModalHeader,
    ModalBody,
    ModalFooter,
    Card,
    CardBody,
    Row
} from 'reactstrap';
import TextField from '@material-ui/core/TextField';
import LinearProgress from '@material-ui/core/LinearProgress';
import Visibility from '@material-ui/icons/Visibility';
import VisibilityOff from '@material-ui/icons/VisibilityOff';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faWindowClose, faUser, faSave } from '@fortawesome/free-solid-svg-icons';

import { useAuth } from "../../../../context/auth";
import { InputAdornment, IconButton } from '@material-ui/core';

const AccountModal = (props) => {
    const authToken = useAuth().authTokens;

    const [isProcessing, setIsProcessing] = useState(false);
    const [isShowNewPass, setIsShowNewPass] = useState(false);
    const [isShowConfirmPass, setIsShowConfirmPass] = useState(false);
    const [isShowOldPass, setIsShowOldPass] = useState(false);
    const [isConfirmError, setIsConfirmError] = useState(false);
    const [isMinimum, setIsMinimum] = useState(false);
    const [newPassword, setNewPassword] = useState('');
    const [oldPassword, setOldPassword] = useState('');

    const toggleShowNewPassword = () => setIsShowNewPass(!isShowNewPass);
    const toggleShowConfirmPassword = () => setIsShowConfirmPass(!isShowConfirmPass);
    const toggleShowOldPassword = () => setIsShowOldPass(!isShowOldPass);

    const changePasswordHandler = (e) => {
        e.preventDefault();
        const token = authToken.access_token;


        if (!isConfirmError && !isMinimum) {
            setIsProcessing(true);

            let newPassData = {
                "password": oldPassword,
                "new_password": newPassword
            }


            API.post("auth/lgu/update-password", newPassData, {
                headers: {
                    'Authorization': 'bearer ' + token,
                }
            }).then(result => {
                setIsProcessing(false);
                if (result.data.status) {
                    Swal.fire(
                        'Transaction Completed!',
                        'Password Successfully Updated!',
                        'success'
                    );

                    props.toggle();
                } else {
                    Swal.fire(
                        'Transaction Failed!',
                        result.data.message,
                        'error'
                    );
                }
            }).catch(e => {
                setIsProcessing(false);
                Swal.fire(
                    'Transaction Failed!',
                    'Failed to Update Password!',
                    'error'
                );
            });
        }
    }

    const checkConfirmPassword = (value) => {
        if (value === newPassword) {
            setIsConfirmError(false)
        } else {
            setIsConfirmError(true)
        }
    }

    return (
        <div>
            <Modal backdrop={"static"} isOpen={props.isOpen} toggle={props.toggle} className="modal-md">
                <ModalHeader><FontAwesomeIcon icon={faUser} /> Change Password</ModalHeader>
                <form onSubmit={(e) => changePasswordHandler(e)} autoComplete="off">
                    <ModalBody>
                        {
                            isProcessing ?
                                <div className="text-center text-primary" style={{ width: "97%", height: "100%", zIndex: "1000", position: "absolute", }}>
                                    <LinearProgress style={{ marginTop: "-1%", }} />
                                    <p>Saving New Record...</p>
                                </div>
                                :
                                null
                        }
                        <Row className="p-2 pt-0">
                            <div className="col-md-12">
                                <Row>
                                    <div className="col-md-12 p-1">
                                        <Card className="p-1">
                                            <CardBody className="p-1 m-1">                                                
                                                <div>
                                                    <TextField
                                                        type={isShowNewPass ? "text" : "password"}
                                                        name="new_password"
                                                        label="New Password"
                                                        onChange={(e) => {
                                                            setNewPassword(e.target.value);
                                                            (e.target.value.length < 6) ? setIsMinimum(true) : setIsMinimum(false)
                                                        }}
                                                        error={isMinimum}
                                                        helperText="New PASSWORD should be atleast 6 characters"
                                                        fullWidth
                                                        required
                                                        InputProps={{
                                                            endAdornment: <InputAdornment position="end">
                                                                <IconButton onClick={toggleShowNewPassword}>
                                                                    {isShowNewPass ? <Visibility /> : <VisibilityOff />}
                                                                </IconButton>
                                                            </InputAdornment>
                                                        }}
                                                    />
                                                </div>
                                                <div>
                                                    <TextField
                                                        error={isConfirmError}
                                                        type={isShowConfirmPass ? "text" : "password"}
                                                        name="confirm_password"
                                                        label="Confirm Password"
                                                        onChange={(e) => checkConfirmPassword(e.target.value)}
                                                        helperText="Input should match your new PASSWORD"
                                                        fullWidth
                                                        required
                                                        InputProps={{
                                                            endAdornment: <InputAdornment position="end">
                                                                <IconButton onClick={toggleShowConfirmPassword}>
                                                                    {isShowConfirmPass ? <Visibility /> : <VisibilityOff />}
                                                                </IconButton>
                                                            </InputAdornment>
                                                        }}
                                                    />
                                                </div>
                                                <div>
                                                    <TextField
                                                        name="old_password"
                                                        label="Old Password"
                                                        type={isShowOldPass ? "text" : "password"}
                                                        onChange={(e) => setOldPassword(e.target.value)}
                                                        helperText="Your current PASSWORD"
                                                        fullWidth
                                                        required
                                                        InputProps={{
                                                            endAdornment: <InputAdornment position="end">
                                                                <IconButton onClick={toggleShowOldPassword}>
                                                                    {isShowOldPass ? <Visibility /> : <VisibilityOff />}
                                                                </IconButton>
                                                            </InputAdornment>
                                                        }}
                                                    />
                                                </div>
                                            </CardBody>
                                        </Card>
                                    </div>
                                </Row>
                            </div>
                        </Row>
                    </ModalBody>
                    <ModalFooter>
                        <Button type="submit" color="primary" disabled={isProcessing} style={{ minWidth: "120px" }}>
                            <FontAwesomeIcon icon={faSave} /> Save
                                </Button>{' '}
                        <Button color="danger" onClick={props.toggle} disabled={isProcessing} style={{ minWidth: "120px" }}>
                            <FontAwesomeIcon icon={faWindowClose} /> Close
                        </Button>
                    </ModalFooter>
                </form>
            </Modal>
        </div>
    );


}

export default AccountModal;