import React, { useState } from 'react';

import TextField from '@material-ui/core/TextField';
import MenuItem from '@material-ui/core/MenuItem';


const TaskForceUnitType = (props) => {
    const types = [
        {
            value: "province",
            label: "Province"
        },
        {
            value: "city",
            label: "City/Municipality"
        },
    ];

    const [selectedUnit, setSelectedUnit] = useState('');

    return (
        <TextField
            select
            label="Task Force Unit"
            placeholder="Task Force Unit"
            value={selectedUnit}
            onChange={(e) => {
                props.unitTypeHandler(e.target.value);
                setSelectedUnit(e.target.value);
            }}
            name="taskforce"
            required
            fullWidth
        >
            {types.map((option) => (
                <MenuItem key={option.value} value={option.value}>
                    {option.label}
                </MenuItem>
            ))}
        </TextField>
    );
}

export default TaskForceUnitType;