import React from 'react';
import { Link } from 'react-router-dom';
import { Col, Row, Spinner, Form } from 'reactstrap';
import { Button, TextField } from '@material-ui/core';
import CheckIcon from '@material-ui/icons/Check';

const EmailVerification = (props) => {
    
    return (
        <div>
            {props.visibility &&
                <Row className="pl-4 pr-4">
                    <Col xs={12} className="mt-2 ml-2 text-left">
                        <h4 className="mt-2 ml-4 mr-4 text-primary" style={{ fontWeight: "bold" }}>Step 1: Email Verification</h4>
                    </Col>
                    <Col xs={12} className="pl-5 pr-5">
                        <Form onSubmit={(e) => props.verify(e)} autoComplete="off">
                            <TextField
                                required
                                type="email"
                                label="Email"
                                defaultValue={props.emailInput}
                                placeholder="Enter your valid email address"
                                name="email"
                                className="mb-2"
                                fullWidth
                                onChange={() => props.handleEmailVerificationError() }
                            />
                            <Button type="submit" variant="contained" color="primary" className="pt-1" disabled={props.isOnVerify} fullWidth>
                                {props.isOnVerify ? <div className="text-center mt-1"><Spinner style={{ width: '1.5rem', height: '1.5rem' }} color="primary" /></div> : <div className="pt-1"><span>Verify Email <CheckIcon /></span></div>}
                            </Button>
                            <br />
                            <br />
                            { !props.isOnVerify && 
                                <div className="text-left">
                                    Already registered?
                                    <Link to="/login" className="ml-1">
                                            Login here
                                    </Link>
                                </div>
                            }
                        </Form>
                    </Col>
                </Row>
            }
        </div>
    );
}

export default EmailVerification;