import React from 'react';
import TermHeaders from '../TermHeaders';
import TermTexts from '../TermTexts';

const UnlawfulActivity = (props) => {
    return (
        <div className="text-justify">
            <TermHeaders>11. Unlawful Activity</TermHeaders>
            <TermTexts>
                We reserve the right to investigate complaints or reported violations of this Agreement and to take any action we deem appropriate,
                including but not limited to reporting any suspected unlawful activity to law enforcement officials, regulators, or other third parties
                and disclosing any information necessary or appropriate to such persons or entities relating to your profile, email addresses, usage history, posted materials, and any other information.
            </TermTexts>
        </div>
    )
}

export default UnlawfulActivity;