import React from 'react';
import TermHeaders from '../TermHeaders';
import TermTexts from '../TermTexts';

const NonTransferable = (props) => {
    return (
        <div className="text-justify">
            <TermHeaders>12. Non-transferable</TermHeaders>
            <TermTexts>
                Your right to use the Site is not transferable or assignable. 
                Any password or right given to you to obtain information or documents is not transferable or assignable.
            </TermTexts>
        </div>
    )
}

export default NonTransferable;