import React from 'react';
import TermHeaders from '../TermHeaders';
import TermTexts from '../TermTexts';

const Copyright = (props) => {
    return (
        <div className="text-justify">
            <TermHeaders>3. Copyright</TermHeaders>
            <TermTexts>
                The contents, organization, graphics, design, compilation, digital conversion and other matters related to the Site are protected under
                applicable copyrights, trademarks and other proprietary (including but not limited to intellectual property) rights.
                The copying, redistribution, use, or publication by you of any such matters or any part of the Site, except as allowed by Section 4 below,
                is strictly prohibited.
            </TermTexts>
        </div>
    )
}

export default Copyright;