import React from 'react';
import TermHeaders from '../TermHeaders';
import TermTexts from '../TermTexts';

const Acceptance = (props) => {
    return (
        <div className="text-justify">
            <TermHeaders>2. Acceptance</TermHeaders>
            <TermTexts>
                You agree to the terms and conditions outlined in the Terms of Use Agreement (Agreement).
                This Agreement constitutes the entire and only agreement between the System and you, and supersedes
                all prior or contemporaneous agreements, representations and understandings with respect to the Site,
                the content, products or services provided by or through the Site, and the subject matter of this Agreement.
                This Agreement may be amended at any time by us from time to time without prior notice to you.
            </TermTexts>
        </div>
    )
}

export default Acceptance;